// Tabs in page about us ========================================================================================================================================================

if(document.querySelectorAll('.partners').length > 0) {
  const tabButtons = document.querySelectorAll(".tab__links");

    for (let i = 0; i < tabButtons.length; i++) {
      tabButtons[i].addEventListener("click", function () {
        let tabContent = document.querySelector('#' + tabButtons[i].dataset.tab)
        let allTabContent = document.querySelectorAll(".tab__content");
        let allTabButtons = document.querySelectorAll(".tab__links");

        const triangels1 = document.querySelector('.triangels1'),
              triangels2 = document.querySelector('.triangels2'),
              triangels3 = document.querySelector('.triangels3'),
              maxX = 600,
              minX = -200,
              maxY = 200,
              minY = -50;

        const x1 = Math.floor(Math.random() * (maxX - minX) + minX),
              x2 = Math.floor(Math.random() * (maxX - minX) + minX),
              x3 = Math.floor(Math.random() * (maxX - minX) + minX),
              y1 = Math.floor(Math.random() * (maxY - minY) + minY),
              y2 = Math.floor(Math.random() * (maxY - minY) + minY),
              y3 = Math.floor(Math.random() * (maxY - minY) + minY);

        for (let j = 0; j < allTabContent.length; j++) {
          allTabContent[j].style.display = "none";
        }

        for (let k = 0; k < allTabButtons.length; k++) {
          allTabButtons[k].classList.remove("active");

          triangels1.style.left = x1 + 'px';
          triangels1.style.top = y1 + 'px';
          triangels1.style.transform = `rotate(${x1}deg)`;

          triangels2.style.left = x2 + 'px';
          triangels2.style.bottom = y2 + 'px';
          triangels2.style.transform = `rotate(${x2}deg)`

          triangels3.style.left = x3 + 'px';
          triangels3.style.bottom = y3 + 'px';
          triangels3.style.transform = `rotate(${x3}deg)`
        }

        tabContent.style.display = "block";
        this.classList.add("active");
      });
    }

    document.querySelector(".tab__links").click();
}

// STATISTIC ANIMATE ========================================================================================================================================================

(function ($) {
  let a = 0;
  let figuresWrap = $(".welcome__items");
  $(document).ready(function () {
    function count() {
      if (figuresWrap.length > 0) {
        let oTop = figuresWrap.offset().top - window.innerHeight + 50;
        if (a == 0 && $(window).scrollTop() > oTop) {
          $(".welcome__item > .welcome__numbers b").each(function () {
            let $this = $(this),
              countTo = $this.text();
            $({
              // countNum: $this.text()
              countNum: 0,
            }).animate(
              {
                countNum: countTo,
              },
              {
                duration: 3000,
                easing: "swing",
                step: function () {
                  $this.text(Math.floor(this.countNum));
                },
                complete: function () {
                  $this.text(this.countNum);
                  //alert('finished');
                },
              }
            );
          });

          a = 1;
        }
      }
    }
    count();
    $(window).on("scroll", function () {
      count();
    });
  });
})(jQuery);

// Show include list  ========================================================================================================================================================

function showList(list, header, btn) {
  const includeList = document.querySelectorAll(list),
        includeHeader = document.querySelectorAll(header),
        includeBtn = document.querySelector(btn);

  includeBtn.addEventListener('click', () => {
    includeHeader.forEach(head => {
      head.classList.toggle('show');
    })
    includeList.forEach(list => {
      list.classList.toggle('show');
    })

    includeBtn.classList.toggle('show');
  })
}

if (document.querySelectorAll('.include').length > 0) {
  showList('.include__list', '.include__header','.include__btn' );
}

if (document.querySelectorAll('.our-private').length > 0) {
  showList('.our-private__list', '.our-private__header','.our-private__btn');
}

// Go to welcome section =======================================================================================================================================================

if(document.querySelectorAll('.hero').length > 0) {
  const btnWelcome = document.querySelector('.scroll-down');

  btnWelcome.addEventListener('click', (e) => {
  e.preventDefault();

  const headerHeight = document.querySelector('.header').clientHeight;
  const heroHeight = document.querySelector('.hero').clientHeight;

  window.scrollTo(0, heroHeight - (headerHeight / 2));
})
}

